import "./index.css"

export const SingleOrderCart = (props) => {
  const { photoURL, productName, productAttribute, count, realPrice, discountPrice } = props
  return (
    <div className='singleOrderCart-container card mt-2 mb-2'>
      <div className='product-image'>
        <img src={process.env.REACT_APP_BASE_URL + "/" + photoURL} alt="product-image" />
      </div>
      <div className='product-details'>
        <span>{productName}</span>
        <span className="mb-4" style={{ textAlign: "center" }}>{productAttribute}</span>
        <div>
          <span>Quantity :</span>
          <span className='card ms-2' style={{ width: "2rem", height: "1.4rem", textAlign: "center" }}>{count}</span>
        </div>
      </div>
      <div className='price'>
        <del>&#8377; {realPrice}</del>
        <span>&#8377; {discountPrice}</span>
      </div>
    </div>
  )
}
