import "./index.css"
import { useParams, useNavigate } from "react-router-dom"
import { useEffect, useState } from "react"
import Axios from "../../api/Axios"
import { SingleOrderCart } from "../../components/SingleOrderCart"
import trackUserEvent from "../../helpers/trackUserEvent"

const axios = new Axios();

const SingleOrder = () => {

    const { id } = useParams()
    const navigator = useNavigate()
    const [shippingAddress, setShippingAddress] = useState({})
    const [billingAddress, setBillingAddress] = useState({})
    const [orderDetails, setOrderDetails] = useState([])
    const [orderStatus, setOrderStatus] = useState()
    const [productPrice, setProductPrice] = useState({})

    const getUserOrderById = async () => {
        const options = {
            method: "GET"
        };
        try {
            const result = await axios.callApi(
                `/user/getOrderById/${id}`,
                options
            );
            if (result.status == 200) {
                setShippingAddress(result.data.data.addressDetails.shippingAddress)
                setBillingAddress(result.data.data.addressDetails.billingAddress)
                setOrderStatus(result.data.data.orderStatus)
                setOrderDetails(result.data.data.orderDetails)
                setProductPrice({
                    totalAmount: result.data.data.orderTotalAmount,
                    deliveryCharges: result.data.data.deliveryCharges,
                    orderAmount: result.data.data.orderAmount,
                    orderAmountWithoutDiscount: result.data.data.orderAmountWithoutDiscount
                })
            }
            else {
                navigator("*")
            }
        }
        catch (error) {
            console.error("Something went wrong")
        }
    }

    useEffect(() => {
        if (id)
            getUserOrderById()
    }, [])

    useEffect(() => {
        trackUserEvent("View Single Order")
    },[])

    return (
        <div className="single-order-container">
            <div className="first-section">
                <div>
                    <h3>Delivery Address</h3>
                    <span>{shippingAddress.firstName + " " + shippingAddress.lastName}</span><br />
                    <span>{shippingAddress.streetAddress + ", " + shippingAddress.city + "-" + shippingAddress.pincode + ", " + shippingAddress.state + ", " + shippingAddress.country}</span><br />
                    <span>Phone number : {shippingAddress.contact}</span>
                </div>
                <div className="order-status">
                    <h3>Order Status</h3>
                    <span className="mt-2">{orderStatus}</span><br />
                    {/* <button className='btn btn-dark mt-2'>Download Invoice</button> */}
                </div>
            </div>
            <div className="second-section mt-5">
                <h4>Product Details</h4>
                {orderDetails.map((e) => <SingleOrderCart photoURL={e.productImage} productName={e.productName} productAttribute={Object.values(e.attributeCombination)[0]} count={e.count} realPrice={e.productPrice} discountPrice={e.productDiscountPrice} />)}
            </div>
            <div className="third-section mt-5">
                <h4>Price Details</h4>
                <div className="price-details ms-3">
                    <span>List price : &#8377; {productPrice.orderAmountWithoutDiscount}</span>
                    <span>Selling price : &#8377; {productPrice.orderAmount}</span>
                    <span>Shipping fee : &#8377; {productPrice.deliveryCharges}</span>
                    <span>Total Amount : &#8377; {productPrice.totalAmount}</span>
                </div>
            </div>
        </div>
    )
}

export default SingleOrder
